import { defineComponent } from "vue";
import ConfirmPopUp from "@/views/PopUp/ConfirmPopUp.vue";
import { useStore } from "vuex";
import { WNInterface } from "vue-wni";
import Loader from "@/views/Loder/Loader.vue";
let store;
//이메일 , 패스워드 , 이름 체크
//const Email_REGX = /^[a-z0-9_+.-]+@([a-z0-9-]+\.)+[a-z0-9]{2,4}$/; //이메일 정규표현식
//const PASSWORD_REGX = /^(?=.*[a-zA-Z])(?=.*\d)(?=.*\W).{8,20}$/; //비밀번호 정규표현식
export default defineComponent({
    name: "Login",
    data() {
        return {
            inputId: '',
            inputPw: '',
            emailCheck: false,
            pwCheck: false,
            showModal: false,
            popUpContent: '',
            loginToggle: false,
            fcmToken: ''
        };
    },
    components: {
        ConfirmPopUp,
        Loader
    },
    computed: {
        //최신버전 팝업
        confirmPopUpShowModal() {
            return store.getters["ConfirmPopUp/getConfirmModal"];
        }
    },
    async created() {
        store = useStore();
        //스크립트 추가
        this.addScript();
        this.fcmToken = '';
        this.fcmToken = await this.$storage.getItem("fcmToken");
    },
    methods: {
        //C:\Users\samsung\PhpstormProjects\ocuPushFront\frontend\src\views\Login\nice.nuguya.oivs.crypto.js
        //자바 스크립트 추가해놓음!
        addScript: function () {
            let scripts = [
                'https://secure.nuguya.com/nuguya/nice.nuguya.oivs.crypto.js',
                'https://secure.nuguya.com/nuguya/nice.nuguya.oivs.msg.js',
                'https://secure.nuguya.com/nuguya/nice.nuguya.oivs.util.js',
            ];
            for (let i = 0, len = scripts.length; i < len; i++) {
                let script = document.createElement('script');
                /*  script.setAttribute('type', 'text/javascript');
                  script.setAttribute('src', scripts[i]);
                 */
                script.src = scripts[i];
                script.async = true;
                document.head.appendChild(script);
            }
        },
        async loginBtn() {
            this.emailCheck && this.pwCheck ? await this.pushLogin() : await this.loginFail();
        },
        //로그인 시도
        async pushLogin() {
            if (!this.loginToggle) {
                //$("#encryptData").value = encrypt
                //여기 부분 주석 풀어주시면 됩니다.!!!! inputID - 사용자 입력 아이디 값 / this.inputPw 사용자 입력 비밀번호 값
                const encrypt = makeSendInfo(this.inputId, this.inputPw); // crypto lib에서 제공하는 함수
                if (this.fcmToken === null) {
                    this.fcmToken = "notToken";
                }
                this.loginToggle = true;
                const VALUE = await store.dispatch("Login/login", JSON.stringify({ userId: this.inputId, userPw: this.inputPw, fcmToken: this.fcmToken, encrypt: encrypt }));
                this.loginToggle = false;
                await this.$storage.setItem('userId', this.inputId, { expire: '90d' });
                if (VALUE !== "error" && VALUE !== "loginFail") {
                    if (VALUE) {
                        await this.$storage.setItem('userId', this.inputId, { expire: '90d' });
                        await this.$storage.setItem('accessToken', VALUE.datas[0]['accessToken'], { expire: '90d' });
                        await this.$storage.setItem('refreshToken', VALUE.datas[0]['refreshToken'], { expire: '90d' });
                        const VALUE2 = await store.dispatch("UserInfo/getUserInfo", JSON.stringify({
                            userId: this.inputId,
                            fcmToken: this.fcmToken
                        }));
                        if (VALUE2 !== "error") {
                            await this.$storage.setItem('accessToken', VALUE2.datas[0]['accessToken'], { expire: '90d' });
                            //자동 로그인
                            await this.$router.replace({ name: 'Home' });
                        }
                        else {
                            this.serverError();
                        }
                    }
                    else {
                        this.serverError();
                    }
                }
                else if (VALUE === "loginFail") {
                    this.loginFail();
                }
                else {
                    this.serverError();
                }
            }
        },
        //로그인 실패
        loginFail() {
            this.popUpDialog('아이디 비밀번호를 다시 확인해주세요.');
        },
        //탈퇴한 회원
        withdrawUser() {
            this.popUpDialog('이미 탈퇴한 회원입니다.');
        },
        //서버 에러
        serverError() {
            this.popUpDialog('서버 에러입니다.');
        },
        popUpDialog(content) {
            store.dispatch("ConfirmPopUp/confirmPopUp", { showModal: true, content: content });
            this.wnDialogShow();
        },
        async wnDialogShow() {
            await WNInterface.execute("wnDialogShow", {
                callback: WNInterface.cb(async (data) => {
                    if (data.status == "CLOSE") {
                        store.dispatch("ConfirmPopUp/confirmPopUp", { showModal: false });
                    }
                })
            });
        },
    },
    watch: {
        //이메일 체크
        inputId: function () {
            //Email_REGX.test(this.inputId) ? this.emailCheck = true : this.emailCheck = false;
            this.inputId.length > 2 ? this.emailCheck = true : this.emailCheck = false;
        },
        //비밀번호 정규표현식 체크
        inputPw: function () {
            //PASSWORD_REGX.test(this.inputPw) ? this.pwCheck = true : this.pwCheck = false;
            this.inputPw.length > 2 ? this.pwCheck = true : this.pwCheck = false;
        }
    }
});
